  display: flex;
  flex-direction: column;
  align-items: flex-start
}

.label {
  font-family: inherit;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  text-transform: capitalize;
  color: #fbfbff;
  color: var(--clr-text)
}

.vote-field {
  position: relative
}

.vote-field__label {
  position: absolute;
  top: 16px;
  left: 24px;
  z-index: 10
}

.vote-field__btns {
  position: absolute;
  top: 50%;
  right: 16px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  display: flex;
  grid-gap: 8px;
  gap: 8px
}

.modal {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0,0,0,.2);
  z-index: 1201
}

.modal-content {
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  background: #262730;
  background: var(--clr-bg-lighter);
  border: 1px solid #2e2f36;
  border: var(--border);
  border-radius: 8px;
  z-index: 2000
}

@media only screen and (max-width:480px) {
  .modal-content {
      width: calc(100% - 20px)
  }
}

.modal-content-size {
  max-width: 400px
}

.modal-topbar {
  margin-bottom: 16px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between
}

.modal-title {
  font-weight: 700;
  font-size: 18px;
  text-transform: capitalize;
  color: #fff
}

.modal-body {
  max-height: 328px;
  overflow-y: auto
}

.modal-body::-webkit-scrollbar {
  width: 8px
}

.modal-body::-webkit-scrollbar-track {
  background: #262730;
  box-shadow: inset 0 0 6px rgba(38,39,48,.3);
  border-radius: 8px
}

.modal-body::-webkit-scrollbar-thumb {
  background: rgba(255,255,255,.2);
  border-radius: 8px;
  box-shadow: inset 0 0 6px rgba(255,255,255,.2)
}

.modal-body::-webkit-scrollbar-thumb:hover,.token-list::-webkit-scrollbar-thumb:hover {
  background: #a7a7a7
}

.modal-body p:first-child {
  margin-top: 0
}

.modal-body li {
  margin-left: 24px
}

.modal-close {
  background: 0 0;
  border: 0;
  cursor: pointer
}

.modal-close:focus,.shoulder__menu__list button:focus {
  outline: 0
}

.modal-close:focus svg {
  stroke: var(--primary-color-lightest)
}

.modal-close svg,.open-wallet-modal-close svg {
  display: block;
  width: 24px;
  height: 24px;
  stroke-width: 1.5;
  stroke-linecap: round;
  stroke-linejoin: round
}

.modal-close svg {
  stroke: #fff;
  stroke: var(--clr-white);
  transition: stroke .2s ease-in-out
}

.modal-ctas,.shoulder__item {
  align-items: center;
  display: flex
}

.modal-ctas {
  margin-top: 16px;
  justify-content: flex-end;
  grid-gap: 8px;
  gap: 8px
}

.shoulder__item {
  justify-content: center;
  width: 40px;
  height: 40px;
  border: 1px solid var(--border);
  border-radius: 32px;
  transition: border-color .2s ease-in-out
}

.shoulder__item:hover {
  border-color: #fff;
  border-color: var(--clr-white);
  fill: var(--clr-text-4)
}

.shoulder__item svg {
  display: block;
  width: 20px;
  height: 20px
}

.shoulder__menu {
  position: relative
}

.shoulder__menu:focus .shoulder__menu__wrapper,.shoulder__menu:focus-within .shoulder__menu__wrapper,.shoulder__menu:hover .shoulder__menu__wrapper {
  pointer-events: all
}

.shoulder__menu:focus .shoulder__menu__list,.shoulder__menu:focus-within .shoulder__menu__list,.shoulder__menu:hover .shoulder__menu__list {
  opacity: 1;
  pointer-events: all
}

button#info {
  margin-right: .5rem
}

label.options-label {
  text-align: left;
  justify-content: center;
  align-content: center;
  flex-direction: row;
  line-height: 3rem;
  text-transform: uppercase;
  font-size: smaller;
  font-weight: 500
}

.modal-body li,.modal-body p {
  text-align: justify
}